
import React from "react"
import axios from 'axios'


export default class LatestRepo extends React.Component {
    constructor(){
        super();
        this.state = {
            latestrepo: [],
        };

    }

    componentDidMount(){
        axios.get(`https://api.github.com/users/dev-apu/repos?per_page=8`)
        .then(response => response.data)
        .then((data) => {
           this.setState({
            latestrepo: data,
           })
        }
       )
       .catch(err => console.log(err));
    }

    render() {
        const {latestrepo} = this.state;

        console.log(latestrepo)
        return (
            <section className="latestrepo bg-grey">
            <div className="container">
                  <div className=""><h2 className="title">Latest Repository</h2></div>
                  <br />
                <div className="list is-hoverable">
                 

                {latestrepo.map((repo, key) => (
                   <div className="list-item" key={repo.id}>
                   <div className="columns is-desktop">
                       
                    <div className="column is-10">
                        <div className="inlinediv">
                    {/* <img src={repo.owner.avatar_url} style={{height:30, }}/> */}
                    </div>

                    <div className="inlinediv">
                    
                        <h3 className="customreponame"><span role="img">💻</span> <span>{repo.name}</span> - { repo.description ? repo.description : 'No Description'}</h3>
                        {/* <span>Description : {repo.description}</span> */}
                        {/* <span>Description : { repo.description ? repo.description : 'No Description'}</span> */}
                    </div>
                    </div>
                    <div className="column ">
                        <div className="buttons">
                        <span className="tag is-link is-small">Clone</span>&nbsp;
                        <span className="tag is-black is-small">{repo.language ? repo.language : 'Not Specified'}</span>
                        </div>

                    </div>
                    </div>
                   </div>
                    ))} 

                    
                    </div>
                    <div className="column is-12">
                    <button className="button is-dark">View More..</button>
                    </div>
            </div>
            </section>
        )
    }
}
